/* Password container styles */
.main-container {
  margin: 0;
  padding-top: 60px;
  /* Adjust this value based on the height of your fixed header */
  position: relative;
}


.password-game-container {
  text-align: center;
  padding: 20px;
  margin: auto;
  width: 50%;
  margin-top: 50px;
  border-radius: 15px;
  background-color: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  /* Add a subtle box shadow */
  z-index: 1;
}

/* Input styles */
.default-input {
  height: 40px;
  width: 50%;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px;
  font-size: 16px;
  outline: none;
  /* Remove the input outline on focus */
}

/* Rules container styles */
.rules-container {

  margin-top: 10px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  /* Add a subtle box shadow */
}

/* Rule container styles */
.rule-container {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 10px;
  font-size: 18px;
  transition: background-color 0.3s, transform 0.3s;
  white-space: normal;
  /* Prevent content from wrapping to the next line */
  overflow: hidden;
  /* Hide any overflow content */
}

.rule-id {
  font-weight: bold;
  /* Make rule id bold */
}

.rule-id-container {
  padding-right: 10px;
  /* Adjust the spacing between rule id and rule */
  border-right: 2px solid #ccc;
  /* Add a vertical line between rule id and rule */
  margin-right: 10px;
  /* Add some margin for spacing */
  white-space: nowrap;
  /* Prevent content from wrapping to the next line */

}

.img {
  max-width: 100%;
  /* Make sure the image does not exceed the width of the container */
  height: auto;
  /* Maintain the aspect ratio */
  display: block;
  /* Remove any extra spacing caused by inline elements */
  margin: 0 auto;
  /* Center the image horizontally within the container */
}



