.rule-container:hover {
  background-color: #f9f9f9;
  /* Change the background color on hover */
  transform: scale(1.02);
  /* Slightly increase the size on hover */
}

/* Active rule container styles */
.active-rule-container {
  background-color: #daffda;
  /* Set background color for active rule container */
}

.rule-container.inactive-rule-container {
  background-color: #ffcccc;
  /* Set background color for inactive rule container */
}

/* Active rule styles */
.active-rule {
  color: green;
  font-weight: bold;
}

/* Inactive rule styles */
.inactive-rule {
  color: #555;
}

.rule-container:hover {
  background-color: #f9f9f9;
  /* Change the background color on hover */
  transform: scale(1.02);
  /* Slightly increase the size on hover */
}

/* Win message styles */
.win-message {
  color: blue;
  font-weight: bold;
}

.grade-display-container {
  width: 20%;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 10px;
  margin-bottom: 10px;
}

.merlion-container {
  position: fixed;
  bottom: 0;
  left: 0;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  /* Adjust duration and easing as needed */
  pointer-events: none;
  /* Prevents the container from blocking interactions */
  z-index: 0;
  /* Set a lower z-index value */
}

.merlion-container.show {
  opacity: 1;
  pointer-events: auto;
  /* Allow interactions when visible */
}

.merlion-image {
  display: none;
  width: 30%;


}

.merlion-container.show .merlion-image {
  display: block;
}

.city-container {
  position: fixed;
  top: 0;
  right: -230px;
  opacity: 0;
  transform: rotate(270deg);
  transition: opacity 1s ease-in-out;
  /* Adjust duration and easing as needed */
  pointer-events: none;
  /* Prevents the container from blocking interactions */
}

.city-container.show {
  opacity: 1;
  pointer-events: auto;
  /* Allow interactions when visible */
}

.city-image {
  display: none;
  width: 45%;
}

.city-container.show .city-image {
  display: block;
}

.satay-container {
  opacity: 0;
  position: fixed;
  top: 300px;
  left: 20px;
  transform: rotate(25deg);
  transition: opacity 1s ease-in-out;
  /* Adjust duration and easing as needed */
  pointer-events: none;
  /* Prevents the container from blocking interactions */
}

.satay-container.show {
  opacity: 1;
  pointer-events: auto;
  /* Allow interactions when visible */
}

.satay-image {
  display: none;
  width: 20%;
}

.satay-container.show .satay-image {
  display: block;
}